"use client";
import React, { useState, useEffect, useRef } from "react";
import { motion } from "framer-motion";
import { ChevronLeft, ChevronRight } from 'lucide-react';
import { cn } from '@/lib/utils';
import axios from 'axios';

const SubcategoryTabs = ({ categoryId, selectedSubcategory, setSelectedSubcategory }) => {
  const [subcategories, setSubcategories] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const scrollContainerRef = useRef(null);
  const [isMobile, setIsMobile] = useState(false);
  const [needsScrolling, setNeedsScrolling] = useState(false);

  // Fetch subcategories when categoryId changes
  useEffect(() => {
    const fetchSubcategories = async () => {
      if (!categoryId || categoryId === 'all') {
        setSubcategories([]);
        setSelectedSubcategory(null);
        return;
      }

      try {
        setIsLoading(true);
        setError(null);
        
        const response = await axios.get(`http://localhost:4000/lmd/api/v1/retail/categories/${categoryId}/subcategories`, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          }
        });
        
        if (response.data.success) {
          setSubcategories(response.data.data || []);
          // Reset selected subcategory when category changes
          setSelectedSubcategory(null);
        } else {
          setError('Failed to fetch subcategories');
          setSubcategories([]);
        }
      } catch (err) {
        console.error('Error fetching subcategories:', err);
        setError('Failed to load subcategories');
        setSubcategories([]);
      } finally {
        setIsLoading(false);
      }
    };

    fetchSubcategories();
  }, [categoryId, setSelectedSubcategory]);

  // Check if we're on mobile and if scrolling is needed
  useEffect(() => {
    const checkScreenSize = () => {
      setIsMobile(window.innerWidth < 768);
      
      // Check if scrolling is needed
      if (scrollContainerRef.current) {
        const containerWidth = scrollContainerRef.current.clientWidth;
        const scrollWidth = scrollContainerRef.current.scrollWidth;
        setNeedsScrolling(scrollWidth > containerWidth);
      }
    };
    
    // Initial check
    checkScreenSize();
    
    // Add event listener for resize
    window.addEventListener('resize', checkScreenSize);
    
    // Clean up
    return () => {
      window.removeEventListener('resize', checkScreenSize);
    };
  }, [subcategories]);

  // Function to handle scrolling left and right
  const scroll = (direction) => {
    if (scrollContainerRef.current) {
      const scrollAmount = direction === 'left' ? -300 : 300;
      scrollContainerRef.current.scrollBy({
        left: scrollAmount,
        behavior: 'smooth'
      });
    }
  };

  // Don't render if no category is selected or if it's "all"
  if (!categoryId || categoryId === 'all' || subcategories.length === 0) {
    return null;
  }

  if (isLoading) {
    return (
      <motion.div 
        className="mt-4 flex justify-center"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
      >
        <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
      </motion.div>
    );
  }

  if (error) {
    return (
      <motion.div 
        className="mt-4 text-center text-red-600 text-sm"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
      >
        {error}
      </motion.div>
    );
  }

  return (
    <motion.div 
      className="mt-4 relative"
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
    >
      {/* Scroll buttons - only visible when scrolling is needed */}
      {needsScrolling && (
        <div className="hidden md:block">
          <button 
            onClick={() => scroll('left')}
            className="absolute left-0 top-1/2 -translate-y-1/2 z-10 bg-white/80 backdrop-blur-sm rounded-full p-2 shadow-md hover:bg-white transition-colors duration-200"
            aria-label="Scroll left"
          >
            <ChevronLeft className="w-4 h-4 text-gray-600" />
          </button>
          <button 
            onClick={() => scroll('right')}
            className="absolute right-0 top-1/2 -translate-y-1/2 z-10 bg-white/80 backdrop-blur-sm rounded-full p-2 shadow-md hover:bg-white transition-colors duration-200"
            aria-label="Scroll right"
          >
            <ChevronRight className="w-4 h-4 text-gray-600" />
          </button>
        </div>
      )}

      {/* Scrollable container */}
      <div 
        ref={scrollContainerRef}
        className="overflow-x-auto no-scrollbar px-4"
      >
        <div className="flex gap-3">
          {/* "All" option for subcategories */}
          <button
            onClick={() => setSelectedSubcategory(null)}
            className={cn(
              'flex items-center rounded-lg transition-all duration-200 flex-shrink-0',
              selectedSubcategory === null
                ? "bg-blue-600 text-white shadow-md"
                : "bg-gray-100 text-gray-700 hover:bg-gray-200",
              isMobile 
                ? "gap-2 px-3 py-2" 
                : "gap-2 px-4 py-2"
            )}
          >
            <span className={cn(
              'font-medium whitespace-nowrap',
              isMobile ? "text-xs" : "text-sm"
            )}>
              All Subcategories
            </span>
          </button>

          {/* Subcategory buttons */}
          {subcategories.map((subcategory) => {
            const isActive = selectedSubcategory === subcategory._id;
            return (
              <button
                key={subcategory._id}
                onClick={() => setSelectedSubcategory(subcategory._id)}
                className={cn(
                  'flex items-center rounded-lg transition-all duration-200 flex-shrink-0',
                  isActive
                    ? "bg-blue-600 text-white shadow-md"
                    : "bg-gray-100 text-gray-700 hover:bg-gray-200",
                  isMobile 
                    ? "gap-2 px-3 py-2" 
                    : "gap-2 px-4 py-2"
                )}
              >
                <span className={cn(
                  'font-medium whitespace-nowrap',
                  isMobile ? "text-xs" : "text-sm"
                )}>
                  {subcategory.name}
                </span>
              </button>
            );
          })}
        </div>
      </div>
    </motion.div>
  );
};

export default SubcategoryTabs; 