"use client";
import OptimizedBanner from "./OptimizedBanner";

const allTabImages = [
  "/home/all/all-banner3.png",
  "/home/all/all-banner2.png",
  "/home/all/all-banner3.png",
];

const OptimizedAllTabBanner = () => {
  return (
    <div className="w-full">
      <OptimizedBanner
        images={allTabImages}
        category="all"
        autoPlayInterval={3000}
        showNavigation={false}
        height="h-[200px] md:h-[300px] lg:h-[400px]"
        className="all-tab-banner mx-auto px-4"
      />
    </div>
  );
};

export default OptimizedAllTabBanner; 