"use client";
import { useState, useEffect, useCallback, memo } from "react";
import { motion } from "framer-motion";
import Image from "next/image";
import { BannerHeader } from "./BannerHeader";

// Memoized image component for better performance
const BannerImage = memo(({ src, alt, index, isActive, priority = false }) => (
  <div
    className="w-full h-full flex-shrink-0 relative"
    aria-hidden={!isActive}
  >
    <Image
      src={src}
      alt={alt}
      fill
      className="object-cover rounded-2xl"
      priority={priority}
      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 80vw, 70vw"
      placeholder="blur"
      blurDataURL="data:image/jpeg;base64,"
    />
  </div>
));

BannerImage.displayName = "BannerImage";

// Memoized navigation dots component
const NavigationDots = memo(({ total, current, onDotClick }) => (
  <div className="flex space-x-2 mt-4" aria-label="Carousel navigation">
    {Array.from({ length: total }, (_, index) => (
      <motion.button
        key={index}
        className={`h-2 rounded-full transition-colors ${
          current === index
            ? "w-8 bg-blue-500"
            : "w-6 bg-gray-300 hover:bg-gray-400"
        }`}
        onClick={() => onDotClick(index)}
        aria-label={`Go to slide ${index + 1}`}
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.95 }}
      />
    ))}
  </div>
));

NavigationDots.displayName = "NavigationDots";

// Main optimized banner component
const OptimizedBanner = memo(({ 
  images, 
  category = "collection",
  autoPlayInterval = 3000,
  showNavigation = true,
  height = "h-[200px] md:h-[300px] lg:h-[400px]",
  className = ""
}) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isAutoPlaying, setIsAutoPlaying] = useState(true);

  // Memoized auto-play logic
  const nextSlide = useCallback(() => {
    setCurrentIndex((prev) => (prev + 1) % images.length);
  }, [images.length]);

  // Auto-play effect with pause on hover
  useEffect(() => {
    if (!isAutoPlaying) return;

    const interval = setInterval(nextSlide, autoPlayInterval);
    return () => clearInterval(interval);
  }, [isAutoPlaying, autoPlayInterval, nextSlide]);

  // Handle dot click
  const handleDotClick = useCallback((index) => {
    setCurrentIndex(index);
    // Pause auto-play briefly when user interacts
    setIsAutoPlaying(false);
    setTimeout(() => setIsAutoPlaying(true), 5000);
  }, []);

  // Handle keyboard navigation
  const handleKeyDown = useCallback((e) => {
    if (e.key === "ArrowLeft") {
      setCurrentIndex((prev) => (prev - 1 + images.length) % images.length);
    } else if (e.key === "ArrowRight") {
      setCurrentIndex((prev) => (prev + 1) % images.length);
    }
  }, [images.length]);

  return (
    <div className={`banner-container ${className}`}>
      <BannerHeader />
      <div className="banner-content">
        <div
          className="w-full flex flex-col items-center"
          role="region"
          aria-label={`${category} carousel`}
          onMouseEnter={() => setIsAutoPlaying(false)}
          onMouseLeave={() => setIsAutoPlaying(true)}
          onKeyDown={handleKeyDown}
          tabIndex={0}
        >
          <div className={`relative w-full ${height} overflow-hidden rounded-2xl`}>
            <div
              className="flex w-full h-full transition-transform duration-500 ease-out"
              style={{ transform: `translateX(-${currentIndex * 100}%)` }}
            >
              {images.map((src, index) => (
                <BannerImage
                  key={`${src}-${index}`}
                  src={src}
                  alt={`${category} ${index + 1}`}
                  index={index}
                  isActive={index === currentIndex}
                  priority={index === 0}
                />
              ))}
            </div>

            {/* Previous/Next buttons for accessibility */}
            <button
              className="absolute left-2 top-1/2 -translate-y-1/2 bg-black/50 text-white rounded-full p-2 opacity-0 hover:opacity-100 transition-opacity"
              onClick={() => setCurrentIndex((prev) => (prev - 1 + images.length) % images.length)}
              aria-label="Previous slide"
            >
              
            </button>
            <button
              className="absolute right-2 top-1/2 -translate-y-1/2 bg-black/50 text-white rounded-full p-2 opacity-0 hover:opacity-100 transition-opacity"
              onClick={() => setCurrentIndex((prev) => (prev + 1) % images.length)}
              aria-label="Next slide"
            >
              
            </button>
          </div>

          {showNavigation && images.length > 1 && (
            <NavigationDots
              total={images.length}
              current={currentIndex}
              onDotClick={handleDotClick}
            />
          )}
        </div>
      </div>
    </div>
  );
});

OptimizedBanner.displayName = "OptimizedBanner";

export default OptimizedBanner; 