'use client';

import { cn } from '@/lib/utils';

const PlaceholderIcon = ({ className, text = "Icon", size = "md" }) => {
  const sizeClasses = {
    sm: "w-6 h-6 text-xs",
    md: "w-8 h-8 text-sm",
    lg: "w-12 h-12 text-base",
  };

  return (
    <div
      className={cn(
        "bg-gray-200 rounded-full flex items-center justify-center text-gray-600 font-medium",
        sizeClasses[size],
        className
      )}
    >
      {text}
    </div>
  );
};

export default PlaceholderIcon; 