'use client';

import { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import PlaceholderIcon from '@/app/components/ui/PlaceholderIcon';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import { cn } from '@/lib/utils'; 

const CategoryTabs = ({ selectedTab, setSelectedTab }) => {
  const [categories, setCategories] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const scrollContainerRef = useRef(null);
  const [isMobile, setIsMobile] = useState(false);
  const [needsScrolling, setNeedsScrolling] = useState(false);

  // Fetch categories using axios
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        setIsLoading(true);
        setError(null);
        
        const response = await axios.get('http://localhost:4000/lmd/api/v1/retail/categories');
        
        if (response.data.success) {
          setCategories(response.data.data || []);
        } else {
          setError('Failed to fetch categories');
          setCategories([]);
        }
      } catch (err) {
        console.error('Error fetching categories:', err);
        setError('Failed to load categories');
        setCategories([]);
      } finally {
        setIsLoading(false);
      }
    };

    fetchCategories();
  }, []);

  // Check if we're on mobile and if scrolling is needed
  useEffect(() => {
    const checkScreenSize = () => {
      setIsMobile(window.innerWidth < 768);
      
      // Check if scrolling is needed
      if (scrollContainerRef.current) {
        const containerWidth = scrollContainerRef.current.clientWidth;
        const scrollWidth = scrollContainerRef.current.scrollWidth;
        setNeedsScrolling(scrollWidth > containerWidth);
      }
    };
    
    // Initial check
    checkScreenSize();
    
    // Add event listener for resize
    window.addEventListener('resize', checkScreenSize);
    
    // Clean up
    return () => {
      window.removeEventListener('resize', checkScreenSize);
    };
  }, [categories]);

  // Function to handle scrolling left and right
  const scroll = (direction) => {
    if (scrollContainerRef.current) {
      const scrollAmount = direction === 'left' ? -300 : 300;
      scrollContainerRef.current.scrollBy({
        left: scrollAmount,
        behavior: 'smooth'
      });
    }
  };

  // Add "All" category at the beginning
  const allCategories = [
    { _id: 'all', name: 'All', slug: 'all' },
    ...categories
  ];

  if (isLoading) {
    return (
      <div className="mt-6">
        <div className="flex justify-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="mt-6 text-center text-red-600 text-sm">
        {error}
      </div>
    );
  }

  return (
    <div className="mt-6 relative">
      {/* Scroll buttons - only visible when scrolling is needed */}
      {needsScrolling && (
        <div className="hidden md:block">
          <button 
            onClick={() => scroll('left')}
            className="absolute left-0 top-1/2 -translate-y-1/2 z-10 bg-white/80 backdrop-blur-sm rounded-full p-2 shadow-md hover:bg-white transition-colors duration-200"
            aria-label="Scroll left"
          >
            <ChevronLeft className="w-5 h-5 text-gray-600" />
          </button>
          <button 
            onClick={() => scroll('right')}
            className="absolute right-0 top-1/2 -translate-y-1/2 z-10 bg-white/80 backdrop-blur-sm rounded-full p-2 shadow-md hover:bg-white transition-colors duration-200"
            aria-label="Scroll right"
          >
            <ChevronRight className="w-5 h-5 text-gray-600" />
          </button>
        </div>
      )}

      {/* Scrollable container */}
      <div 
        ref={scrollContainerRef}
        className="overflow-x-auto no-scrollbar px-4"
      >
        <div className="flex justify-between gap-4">
          {allCategories.map((category) => {
            const isActive = selectedTab === category._id;
            return (
              <button
                key={category._id}
                onClick={() => setSelectedTab(category._id)}
                className={cn(
                  'flex items-center rounded-xl transition-all duration-200',
                  isActive
                    ? "bg-gray-50 shadow-lg text-black border-2 border-black border-b-4"
                    : "bg-gray-200 bg-gradient-to-r from-yellow-50 to-gray-200 shadow-sm border-b border-yellow-500 hover:bg-gray-100",
                  isMobile 
                    ? "gap-2 px-3 py-2 min-w-0" 
                    : "gap-3 px-5 py-3 min-w-[180px]"
                )}
              >
                <PlaceholderIcon 
                  text={category.name.charAt(0).toUpperCase()} 
                  size={isMobile ? "sm" : "md"}
                  className={cn(
                    isActive ? "bg-blue-100 text-blue-600" : "bg-gray-100 text-gray-600"
                  )}
                />
                <span className={cn(
                  'font-medium whitespace-nowrap',
                  isMobile ? "text-xs" : "text-sm"
                )}>
                  {category.name}
                </span>
              </button>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default CategoryTabs; 