'use client';

import { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import Header from "@/components/home/Header";
import Footer from './components/layout/Footer';
import CategoryTabs from './components/home/CategoryTabs';
import SubcategoryTabs from '@/components/home/SubcategoryTabs';
import { BannerFactory } from '@/components/home/banners';
import ProductCard from '@/app/components/ui/ProductCard';

const Home = () => {
  const [selectedTab, setSelectedTab] = useState('all');
  const [selectedSubcategory, setSelectedSubcategory] = useState(null);
  const [subcategories, setSubcategories] = useState([]);
  const [isLoadingSubcategories, setIsLoadingSubcategories] = useState(false);
  const [subcategoryProducts, setSubcategoryProducts] = useState({});
  const [isLoadingProducts, setIsLoadingProducts] = useState(false);
  
  // New state for featured and best seller products
  const [featuredProducts, setFeaturedProducts] = useState([]);
  const [bestSellerProducts, setBestSellerProducts] = useState([]);
  const [isLoadingFeatured, setIsLoadingFeatured] = useState(false);
  const [isLoadingBestSellers, setIsLoadingBestSellers] = useState(false);

  // Generate dummy products for a subcategory
  const generateDummyProducts = (subcategoryName, count = 6) => {
    const dummyProducts = [];
    const brands = ['Brand A', 'Brand B', 'Brand C', 'Brand D', 'Brand E'];
    const images = [
      '/home/grocery/broccoli.png',
      '/categories/brinjal.png',
      '/categories/beetroot.png',
      '/categories/mango.png',
      '/categories/mangoes.png',
      '/categories/pear.png',
      '/new1.png',
      '/new2.png',
      '/movie2.jpg'
    ];

    for (let i = 1; i <= count; i++) {
      const basePrice = Math.floor(Math.random() * 1000) + 100;
      const discountedPrice = Math.floor(Math.random() * (basePrice - 50)) + 50;
      
      dummyProducts.push({
        _id: `dummy-${subcategoryName}-${i}`,
        name: `${subcategoryName} Product ${i}`,
        description: `This is a dummy ${subcategoryName} product description for product ${i}.`,
        price: {
          base_price: basePrice,
          discounted_price: discountedPrice,
        },
        category: {
          _id: selectedTab,
          name: selectedTab,
          slug: selectedTab
        },
        subcategory: {
          _id: `sub-${i}`,
          name: subcategoryName,
          slug: subcategoryName.toLowerCase().replace(/\s+/g, '-')
        },
        brand: brands[Math.floor(Math.random() * brands.length)],
        images: [{
          url: images[Math.floor(Math.random() * images.length)],
          is_primary: true
        }],
        rating: {
          average: (Math.random() * 2 + 3).toFixed(1),
          count: Math.floor(Math.random() * 100)
        },
        reviews: Math.floor(Math.random() * 100),
        stock: Math.floor(Math.random() * 50),
        is_featured: Math.random() > 0.7,
        is_best_seller: Math.random() > 0.8,
        is_new_arrival: Math.random() > 0.9,
        status: 'active',
        tags: [],
        attributes: [],
        specifications: [],
        vendor_store_id: {
          _id: 'dummy-store',
          store_name: 'Dummy Store',
          fullAddress: 'Dummy Address'
        }
      });
    }

    return dummyProducts;
  };

  // Transform API product data to match ProductCard expectations
  const transformProductData = (product) => {
    return {
      _id: product._id,
      name: product.name,
      description: product.description,
      price: {
        base_price: product.price?.base_price || 0,
        discounted_price: product.price?.discounted_price || product.price?.base_price || 0,
      },
      category: product.category,
      subcategory: product.subcategory,
      brand: product.brand,
      images: product.images || [],
      rating: product.rating || { average: 0, count: 0 },
      reviews: product.rating?.count || 0,
      stock: product.stock || 0,
      is_featured: product.is_featured || false,
      is_best_seller: product.is_best_seller || false,
      is_new_arrival: product.is_new_arrival || false,
      status: product.status || 'active',
      tags: product.tags || [],
      attributes: product.attributes || [],
      specifications: product.specifications || [],
      vendor_store_id: product.vendor_store_id,
      product_type: product.product_type || 'retail'
    };
  };

  // Fetch products for a specific subcategory
  const fetchProductsForSubcategory = async (subcategoryId, subcategoryName) => {
    try {
      // Build query parameters for the API
      const queryParams = new URLSearchParams({
        page: '1',
        limit: '10',
        sort: '-createdAt',
        category: selectedTab,
        subcategory: subcategoryId,
        status: 'active'
      });

      const response = await axios.get(`http://localhost:4000/lmd/api/v1/retail/products?${queryParams}`, {
        headers: {
          'Content-Type': 'application/json',
      
        }
      });

      console.log(`Products response for ${subcategoryName}:`, response.data);

      if (response.data.status === 'success' && response.data.data && response.data.data.length > 0) {
        // Transform the API response to match ProductCard expectations
        const transformedProducts = response.data.data.map(transformProductData);
        return transformedProducts;
      } else {
        // Return dummy products if no real products found
        console.log(`No products found for subcategory ${subcategoryName}, using dummy products`);
        return generateDummyProducts(subcategoryName);
      }
    } catch (err) {
      console.error(`Error fetching products for subcategory ${subcategoryName}:`, err);
      // Return dummy products on error
      return generateDummyProducts(subcategoryName);
    }
  };

  // Fetch and display subcategories when category changes
  useEffect(() => {
    const fetchSubcategories = async () => {
      if (selectedTab && selectedTab !== 'all') {
        try {
          setIsLoadingSubcategories(true);
          const response = await axios.get(`http://localhost:4000/lmd/api/v1/retail/categories/${selectedTab}/subcategories`, {
            headers: {
              'Content-Type': 'application/json',
            
            }
          });
          console.log('Subcategories for category:', selectedTab, response.data);
          if (response.data.success) {
            const subcats = response.data.data || [];
            setSubcategories(subcats);
            
            // Fetch products for each subcategory
            setIsLoadingProducts(true);
            const productsData = {};
            
            for (const subcategory of subcats) {
              const products = await fetchProductsForSubcategory(subcategory._id, subcategory.name);
              productsData[subcategory._id] = products;
            }
            
            setSubcategoryProducts(productsData);
            setIsLoadingProducts(false);
          } else {
            setSubcategories([]);
            setSubcategoryProducts({});
          }
        } catch (err) {
          console.error('Error fetching subcategories:', err);
          setSubcategories([]);
          setSubcategoryProducts({});
        } finally {
          setIsLoadingSubcategories(false);
          setIsLoadingProducts(false);
        }
      } else {
        setSubcategories([]);
        setSubcategoryProducts({});
        setSelectedSubcategory(null);
      }
    };

    fetchSubcategories();
  }, [selectedTab]);

  // Fetch featured and best seller products when "all" category is selected
  useEffect(() => {
    if (selectedTab === 'all') {
      fetchFeaturedProducts();
      fetchBestSellerProducts();
    } else {
      // Clear featured and best seller products when other categories are selected
      setFeaturedProducts([]);
      setBestSellerProducts([]);
    }
  }, [selectedTab]);

  // Fetch featured products
  const fetchFeaturedProducts = async () => {
    try {
      setIsLoadingFeatured(true);
      const response = await axios.get('http://localhost:4000/lmd/api/v1/retail/products/featured', {
        headers: {
          'Content-Type': 'application/json',
        },
        params: {
          limit: 10
        }
      });

      console.log('Featured products response:', response.data);

      if (response.data.status === 'success' && response.data.data && response.data.data.length > 0) {
        const transformedProducts = response.data.data.map(transformProductData);
        setFeaturedProducts(transformedProducts);
      } else {
        console.log('No featured products found, using dummy products');
        setFeaturedProducts(generateDummyProducts('Featured', 8));
      }
    } catch (err) {
      console.error('Error fetching featured products:', err);
      setFeaturedProducts(generateDummyProducts('Featured', 8));
    } finally {
      setIsLoadingFeatured(false);
    }
  };

  // Fetch best seller products
  const fetchBestSellerProducts = async () => {
    try {
      setIsLoadingBestSellers(true);
      const response = await axios.get('http://localhost:4000/lmd/api/v1/retail/products/best-sellers', {
        headers: {
          'Content-Type': 'application/json',
        },
        params: {
          limit: 10
        }
      });

      console.log('Best seller products response:', response.data);

      if (response.data.status === 'success' && response.data.data && response.data.data.length > 0) {
        const transformedProducts = response.data.data.map(transformProductData);
        setBestSellerProducts(transformedProducts);
      } else {
        console.log('No best seller products found, using dummy products');
        setBestSellerProducts(generateDummyProducts('Best Seller', 8));
      }
    } catch (err) {
      console.error('Error fetching best seller products:', err);
      setBestSellerProducts(generateDummyProducts('Best Seller', 8));
    } finally {
      setIsLoadingBestSellers(false);
    }
  };

  // Handle cart and wishlist actions
  const handleAddToCart = (product) => {
    console.log('Add to cart:', product);
  };

  const handleAddToWishlist = (product) => {
    console.log('Add to wishlist:', product);
  };

  // Enhanced Carousel Component with Arrow Navigation
  const SimpleCarousel = ({ products, title }) => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const containerRef = useRef(null);
    const productsPerView = 4; // Number of products visible at once
    const maxIndex = Math.max(0, products.length - productsPerView);

    const scrollToIndex = (index) => {
      if (containerRef.current) {
        const cardWidth = 256; // w-64 = 256px
        const scrollPosition = index * cardWidth;
        containerRef.current.scrollTo({
          left: scrollPosition,
          behavior: 'smooth'
        });
      }
    };

    const handlePrevious = () => {
      const newIndex = Math.max(0, currentIndex - 1);
      setCurrentIndex(newIndex);
      scrollToIndex(newIndex);
    };

    const handleNext = () => {
      const newIndex = Math.min(maxIndex, currentIndex + 1);
      setCurrentIndex(newIndex);
      scrollToIndex(newIndex);
    };

    const canScrollLeft = currentIndex > 0;
    const canScrollRight = currentIndex < maxIndex;

    return (
      <div className="w-full">
        <h2 className="text-xl sm:text-2xl md:text-3xl font-bold mb-2 sm:mb-4">{title}</h2>
        <section className="w-full">
          <div className="w-full max-w-[1500px] mx-auto relative">
            {/* Left Arrow */}
            {canScrollLeft && (
              <button
                onClick={handlePrevious}
                className="absolute left-0 top-1/2 -translate-y-1/2 z-10 bg-white/90 backdrop-blur-sm rounded-full p-2 shadow-lg hover:bg-white transition-colors duration-200 border border-gray-200"
                aria-label="Previous products"
              >
                <ChevronLeft className="w-6 h-6 text-gray-600" />
              </button>
            )}

            {/* Right Arrow */}
            {canScrollRight && (
              <button
                onClick={handleNext}
                className="absolute right-0 top-1/2 -translate-y-1/2 z-10 bg-white/90 backdrop-blur-sm rounded-full p-2 shadow-lg hover:bg-white transition-colors duration-200 border border-gray-200"
                aria-label="Next products"
              >
                <ChevronRight className="w-6 h-6 text-gray-600" />
              </button>
            )}

            {/* Products Container */}
            <div 
              ref={containerRef}
              className="flex space-x-4 overflow-hidden pb-4 px-2"
              style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}
            >
              {products.map((product) => (
                <div key={product._id} className="flex-shrink-0 w-64">
                  <ProductCard
                    product={product}
                    onAddToCart={handleAddToCart}
                    onAddToWishlist={handleAddToWishlist}
                  />
                </div>
              ))}
            </div>

            {/* Hide scrollbar for webkit browsers */}
            <style jsx>{`
              div::-webkit-scrollbar {
                display: none;
              }
            `}</style>
          </div>
        </section>
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Hero Section with Optimized Banner */}
        <section className="mb-12">
          <div className="overflow-hidden shadow-lg">
            <BannerFactory 
              category={selectedTab} 
              className="w-full"
              height="h-[300px] md:h-[400px] lg:h-[500px]"
            />
          </div>
        </section>

        {/* Category Tabs */}
        <section className="mb-8">
          <CategoryTabs selectedTab={selectedTab} setSelectedTab={setSelectedTab} />
        </section>

        {/* Subcategories with GroceryTab-style UI */}
        {selectedTab !== 'all' && (
          <section className="mb-8">
            <div className="p-4 sm:p-6 md:p-8 lg:px-16">
              {isLoadingSubcategories ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                </div>
              ) : subcategories.length > 0 ? (
                subcategories.map((subcategory, index) => (
                  <div key={subcategory._id} className={`w-full ${index > 0 ? 'mt-8 sm:mt-12 md:mt-16' : ''}`}>
                    {isLoadingProducts ? (
                      <div className="flex justify-center py-4">
                        <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
                      </div>
                    ) : (
                      <SimpleCarousel 
                        products={subcategoryProducts[subcategory._id] || []}
                        title={subcategory.name}
                      />
                    )}
                  </div>
                ))
              ) : (
                <div className="text-center py-8 text-gray-500">
                  No subcategories found for this category.
                </div>
              )}
            </div>
          </section>
        )}

        {/* Featured Products - Show when "all" category is selected */}
        {selectedTab === 'all' && (
          <section className="mb-8">
            <div className="p-4 sm:p-6 md:p-8 lg:px-16">
              {isLoadingFeatured ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                </div>
              ) : (
                <SimpleCarousel 
                  products={featuredProducts}
                  title="Featured Products"
                />
              )}
            </div>
          </section>
        )}

        {/* Best Seller Products - Show when "all" category is selected */}
        {selectedTab === 'all' && (
          <section className="mb-8">
            <div className="p-4 sm:p-6 md:p-8 lg:px-16">
              {isLoadingBestSellers ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                </div>
              ) : (
                <SimpleCarousel 
                  products={bestSellerProducts}
                  title="Best Sellers"
                />
              )}
            </div>
          </section>
        )}

        {/* Selected Subcategory Info */}
        {selectedSubcategory && (
          <section className="mb-8">
            <div className="bg-green-50 border border-green-200 rounded-lg p-4">
              <h4 className="text-green-800 font-medium">
                Selected Subcategory: {subcategories.find(sub => sub._id === selectedSubcategory)?.name}
              </h4>
            </div>
          </section>
        )}

      </main>

      <Footer />
    </div>
  );
};

export default Home;
